import styled from 'styled-components';

const AlbumTracksLink = styled.div`
  position: relative;
  padding-top: 9px;
  box-sizing: border-box;
  text-align: center;
  background-color: black;
  color: white;
  cursor: pointer;
`;

export default AlbumTracksLink;



